@import url("https://fonts.googleapis.com/css2?family=Alegreya + Sans + SC:wght@400;500;800");
* {
  box-sizing: border-box;
}

body, html {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

.main {
  position: relative;
  min-height: 100%;
  min-width: 363px;
}

.section {
  display: flex;
}

.container {
  max-width: 1440px;
  margin: auto;
}

.popup {
  /*display: none /**/
  background-color: rgba(18, 18, 18, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
}